
*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#app{
  font-family: 'Rajdhani', sans-serif;
  background-color: #fafafa;
}
.clearfix::after{
  content: "";
  display: block;
  clear: both;
}
main{
  width: 700px;
  margin: 0 auto;
  padding: 50px;
}
.check__wrapper{
  width: 300px;
  height: 200px;
  position: relative;
  border: 7px solid black;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
.picked-check{
  background-image: url(/static/img/editableImage.84ef28b.png);
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}
.check-1{
  top: 52px;
  left: 92px;
  height: 130px;
  width: 51px;
  position: absolute;
}
.check-2{
  height: 27px;
  width: 100%;
  bottom: 0;
  position: absolute;
}
/* ●--------------------● */
/* カラーピッカー            */
/* ●--------------------● */
.color__elm{
  width: 78px;
  height: 78px;
  border-radius: 3px;
}
.picked__wrapper{
  width: 600px;
  padding: 30px 0;
}
.picked__color{
  width: 100px;
  float: left;
}
.picked__value-wrapper{
  width: 500px;
  padding-left: 10px;
  float: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
.picked__value-wrapper div{
  width: 100%;
}
.picked__value-wrapper input{
  width: 80px;
  font-size: 12px;
  padding: 4px;
}
.picked__value__title{
  font-size: 20px;
  color: #aaaaaa;
  font-weight: bold;
  display: inline-block;
  padding-right: 20px;
}
/* ●--------------------● */
/* icons                  */
/* ●--------------------● */
.save-icon{
  width: 20px;
  height: 20px;
  opacity: .6;
}
/* ●--------------------● */
/* saved colors           */
/* ●--------------------● */
.saved-colors__wrapper{
  float: right;
  width: 400px;
  padding-left: 20px;
}
.saved-colors__color{
  width: 40px;
  height: 20px;
  background-color: red;
  border-radius: 3px;
}
.saved-colors__wrapper td{
  padding: 0 10px;
  height: 20px;
  line-height: 20px;
}
/* ●--------------------● */
/* カラーピッカー            */
/* ●--------------------● */
.picker-container{
  /*background-color: #333;*/
  width: 200px;
  float: left;
}
.colorpicker {
  position: relative;
  overflow: hidden;
  /*width: 400px;*/
  /*height: 400px;*/
  background-color: rgb(255, 0, 0);
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgb(0, 0, 0))), -webkit-gradient(linear, left top, right top, from(rgb(255, 255, 255)), to(rgba(255, 255, 255, 0)));
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgb(0, 0, 0)), linear-gradient(to right, rgb(255, 255, 255), rgba(255, 255, 255, 0));
}
.colorpicker-cursor {
  pointer-events: none;
  position: absolute;
  width: 16px;
  height: 16px;
  left: 400px;
  top: -9px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 4px solid white;
  border-radius: 50%;
}
.huepicker {
  position: relative;
  height: 30px;
  margin-top: 20px;
  border-radius: 5px;
  background-image: -webkit-gradient(linear,
    left top, right top,
    from(hsl(360,100%,50%)),
    color-stop(hsl(300,100%,50%)),
    color-stop(hsl(240,100%,50%)),
    color-stop(hsl(180,100%,50%)),
    color-stop(hsl(120,100%,50%)),
    color-stop(hsl(60,100%,50%)),
    to(hsl(0,100%,50%))
  );
  background-image: linear-gradient(to right,
    hsl(360,100%,50%),
    hsl(300,100%,50%),
    hsl(240,100%,50%),
    hsl(180,100%,50%),
    hsl(120,100%,50%),
    hsl(60,100%,50%),
    hsl(0,100%,50%)
  );
}
.huepicker-cursor {
  pointer-events: none;
  position: absolute;
  top: -4px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  height: 100%;
  width: 8px;
  border: 4px solid #666;
  border-radius: 6px;
}

